@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
nav{position:fixed;top:0;left:0;width:30px;height:120px;background:#0000005b;display:flex;flex-direction:column;justify-content:space-around;align-items:center}nav a{width:24px;height:24px}nav a svg{width:24px;height:24px}

.bg-img{background-size:cover !important;background-attachment:fixed !important;display:grid;place-items:center}.bg-img h1{color:#ffffff;text-shadow:2px 2px #1d44c4}.python-bg .bg-img{background:url(/code4all/static/media/black-laptop.e5f719bd.jpg) no-repeat center;height:100vh}.webdev-bg .bg-img{background:url(/code4all/static/media/tea-laptop.2f5697ac.jpg) no-repeat center;height:100vh}.main .bg-img{background:url(/code4all/static/media/phone-laptop.5f49ffe6.jpg) no-repeat center;height:700px}.why .bg-img{background:url(/code4all/static/media/dark-laptop.117af267.jpg) no-repeat center;height:300px}.why .list{display:grid;grid-template-columns:1fr 1fr}@media (max-width: 768px){.why .list{grid-template-columns:1fr}}.about-us .bg-img{background:url(/code4all/static/media/stats-laptop.b1df5a8e.jpg) no-repeat center;height:300px}.title{color:white;text-align:center;padding:5%;font-size:x-large}.text{--padding-width: 5rem;width:calc(100% - calc(2 * var(--padding-width)));padding:2rem var(--padding-width)}.aims{text-align:center}.aims h1{margin-bottom:1rem}.aims ul{list-style:none;margin:0}.aims ul hr{width:20rem;max-width:75%;margin:0.5rem auto;padding:0;border-width:0;border-top:2px dashed black}.about-us{text-align:center}.about-us .text p{padding:1rem 0}.about-us .text button{border:0;outline:0;width:-webkit-max-content;width:-moz-max-content;width:max-content;padding:1rem;background:#82ffa8;border-radius:8px;color:black}.about-us .text button:hover{cursor:pointer}.about-us .text a{width:48px;height:48px;margin-bottom:2rem}.about-us .text a svg{width:48px;height:48px}

.list-item{display:grid;place-items:center}.list-item .bubble{width:16px;height:16px;padding:1rem;margin-bottom:4px;border-radius:50%;background-color:var(--fg);color:black;display:grid;place-items:center;text-align:center}.list-item .content{padding-bottom:1.5rem;text-align:center}


:root{--fg: #82ffa8}*{margin:0;padding:0}body{font-family:'Roboto', sans-serif}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:#888}::-webkit-scrollbar-thumb:hover{background:#555}

